import React from 'react';
import { Card, Typography, Row, Col, Image } from 'antd';
import './Feature.less';

import idCard from "../assets/id-card.png";
import bankCard from "../assets/bank-card.png";
import invoice from "../assets/invoice.png";
import driverCard from "../assets/driver-card.png";
import idVer from "../assets/id-ver.png";
import table from "../assets/table.png";
import seal from "../assets/seal.png";
import report from "../assets/report.png";
import { Link } from 'react-router-dom';

const { Title } = Typography;

const featureConfig = [{
  title: "",
  description: "",
  icon: idCard,
}, {
  title: "",
  description: "OCR",
  icon: bankCard
}, {
  title: "",
  description: "",
  icon: invoice
}, {
  title: "",
  description: "",
  icon: driverCard
}, {
  title: "",
  description: "",
  icon: idVer
}, {
  title: "",
  description: "OCR",
  icon: table
}, {
  title: "",
  description: "",
  icon: seal
}, {
  title: "",
  description: "",
  icon: report
}]

const Feature = () => {
  
  return (
    <div className='container'>
      <Title level={2}>AI </Title>
      <span className='sub-title'></span>
      <div className='feature-container'>
        <Row gutter={[28, 28]}>
          {
            featureConfig.map((feature, index) => (
              <Col span={6} key={index}>
                {/* <Link to='/sample'> */}
                  <Card title={(
                    <div className='title'>
                      <span>{feature.title}</span>
                      <Image src={feature.icon} height="40px" preview={false}></Image>
                    </div>
                  )} className="feature-card">
                    <p className='description'>{feature.description}</p>
                    <span className='go-detail'></span>
                  </Card>
                {/* </Link> */}
              </Col>
            ))
          }
        </Row>
      </div>
    </div>
  )
};

export default Feature;