import React from 'react';
import { List, Table } from 'antd';

const resultConfig = [
  {key: "InvoiceType", name: ""},
  {key: "InvoiceTypeOrg", name: ""},
  {key: "InvoiceCode", name: ""},
  {key: "InvoiceNum", name: ""},
  {key: "InvoiceDate", name: ""},
  {key: "PurchaserName", name: ""},
  {key: "PurchaserRegisterNum", name: ""},
  {key: "PurchaserAddress", name: ""},
  {key: "PurchaserBank", name: ""},
  {key: "Password", name: ""},
  {key: "Province", name: ""},
  {key: "City", name: ""},
  {key: "SheetNum", name: ""},
  {key: "Agent", name: ""},
  {key: "SellerName", name: ""},
  {key: "SellerRegisterNum", name: ""},
  {key: "SellerAddress", name: ""},
  {key: "SellerBank", name: ""},
  {key: "TotalAmount", name: ""},
  {key: "TotalTax", name: ""},
  {key: "AmountInWords", name: "()"},
  {key: "AmountInFiguers", name: "()"},
  {key: "Payee", name: ""},
  {key: "Checker", name: ""},
  {key: "NoteDrawer", name: ""},
  {key: "Remarks", name: ""},
]

const CommodityKeys = [
  'CommodityName',
  'CommodityType',
  'CommodityUnit',
  'CommodityNum',
  'CommodityPrice',
  'CommodityAmount',
  'CommodityTaxRate',
  'CommodityTax'
]

const columns = [{
  title: "",
  dataIndex: 'CommodityName'
},{
  title: "",
  dataIndex: 'CommodityType'
},{
  title: "",
  dataIndex: 'CommodityUnit'
},{
  title: "",
  dataIndex: 'CommodityNum'
},{
  title: "",
  dataIndex: 'CommodityPrice'
},{
  title: "",
  dataIndex: 'CommodityAmount'
},{
  title: "",
  dataIndex: 'CommodityTaxRate'
},{
  title: "",
  dataIndex: 'CommodityTax'
}]

const VatInvoiceResult = ({dataSource}) => {

  const getCommodityData = () => {
    if (!dataSource) return [];
    const data = [];
    const length = dataSource['CommodityName'].length;
    const commodityKeyNums = CommodityKeys.length;
    for(let i = 1; i <= length; i++) {
      let column = {key: i + ""};
      let rowAndWord = {};
      for(let j = 0; j < commodityKeyNums; j++) {
        rowAndWord = dataSource[CommodityKeys[j]].find(row => row.row == i);
        if (!rowAndWord) continue;
        column[CommodityKeys[j]] = rowAndWord.word;
      }
      data.push(column);
    }
    return data;
  }

  return (
    <>
      <List
        size='small'
        bordered
      >
        {
          resultConfig.map(config => (
            <List.Item key={config.key} className='list-item'>
              <span className='vat-invoice-name'>{config.name}</span>
              <span className='value'>{dataSource?.[config.key]}</span>
            </List.Item>
          ))
        }
      </List>
      <Table 
        bordered
        pagination={false}
        size="small"
        columns={columns}
        dataSource={getCommodityData()}
      />
    </>
  )
}

export default VatInvoiceResult;